@import url(https://fonts.googleapis.com/css2?family=Arimo:wght@400;700&display=swap);
body {
  margin: 0;
  /* padding: 0; */
  font-family: 'Arimo', sans-serif, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.background-screen-header {
  z-index: -999;
  background: url("/tele-trading/static/media/HEADER BACKGROUND.b12b3430.jpg") no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
  width: 100%;
}

.transparent-background-header {
  background-color: #eeeeee88;
  height: 100%;
  width: 100%;
}

.home-info {
  color: #050505;
  text-align: center;
}

.home-info h1{
  font-size: 50px;
}

.home-info p{
  font-size: 20px;
}

.logo2 {
  display: block;
  height: 200px;
  margin: 0 auto;  
}

@media screen and (max-width: 820px)  {


  .home-info h1{
    font-size: 40px;
  }
  
  .home-info p{
    font-size: 18px;
  }

  .logo2 {
    height: 180px;
  }
 
}

@media screen and (max-width: 735px)  {
  .home-info h1{
    font-size: 35px;
  }
  
  .home-info p{
    font-size: 18px;
  }

  .logo2 {
    height: 160px;
  }
}

@media screen and (max-width: 599px)  {
  /* .background-screen-header {
    height: 100%;
  } */

  .home-info h1{
    font-size: 32px;
  }
  
  .home-info p{
    font-size: 16px;
  }

}

@media screen and (max-width: 450px)  {

  .home-info h1{
    font-size: 25px;
  }
  
  .home-info p{
    font-size: 15px;
  }
}

@media screen and (max-width: 350px)  {

  .home-info h1{
    font-size: 20px;
  }
  
  .home-info p{
    font-size: 14px;
  }
}
.background-screen-services {
  background-size: cover;
  background-color: #3389dcc2;
  height: 100%;
  width: 100%;

}

.transparent-background-services {
  background-color: #ac302088;
