.map-h2 {
  text-transform: uppercase;
  font-size: 1rem;
  padding: 20px;
  padding-left: 10px;
  text-align: center;
}

.google-map {
  width: 100%;
  height: 60vh;
}

.pin {
  display: flex;
  align-items: center;
  width: 180px;
  color: var(--main-blue);
}

.pin-icon {
  font-size: 4rem;
}

.pin-text {
  font-size: 1.3em;
}

@media screen and (min-width: 799px) {
  .google-map {
    height: 80vh;
  }

  .map-h2 {
    font-size: 1.3rem;
    font-weight: 400;
  }

  .pin {
    width: 15vw;
  }

  .pin-icon {
    font-size: 10vw;
  }
}
.background-screen-contactus {
  z-index: -999;
  background: url("/tele-trading/static/media/CONTACT US BACKGROUND.55195ee2.jpg") no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
  width: 100%;
}

.transparent-backgrund-contactus {
  background-color: #3e699bad;
  height: 100%;
  width: 100%;
}

@media screen and (max-width: 960px)  {
  .background-screen-contactus {
    height: 100%;
  }

  .transparent-backgrund-contactus {
    height: 100%;
  }
}

